'use client';

import {useState} from 'react';
import {useRouter} from 'next/navigation';
import Image from 'next/image';
import {
  FileText,
  FileInput,
  Users,
  Heart,
  FlaskConical,
  Camera,
  Loader2,
} from 'lucide-react';
import {Button} from '@/components/ui/button';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {Textarea} from '@/components/ui/textarea';
import {Label} from '@/components/ui/label';
import {Input} from '@/components/ui/input';
import {useToast} from '@/hooks/use-toast';
import {PageHeader} from '@/components/page-header';

export default function CaseAnalysisPage() {
  const router = useRouter();
  const {toast} = useToast();
  const [isLoading, setIsLoading] = useState(false);
  const [crimeScenePictures, setCrimeScenePictures] = useState<string[]>([]);

  const handlePictureUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const filesArray = Array.from(e.target.files);
      const dataUrls = filesArray.map(file => URL.createObjectURL(file));
      setCrimeScenePictures(prev => [...prev, ...dataUrls]);
      // In a real app, you'd convert these to base64 for the AI flow
    }
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setIsLoading(true);
    toast({
      title: 'Starting Analysis...',
      description:
        'The AI is processing all case data. This may take a moment.',
    });

    // In a future step, we'll gather all form data and send it to the AI.
    // For now, we'll simulate the process and navigate to the report page.
    setTimeout(() => {
      setIsLoading(false);
      router.push('/report');
    }, 3000);
  };

  return (
    <div>
      <PageHeader
        title="Comprehensive Case Analysis"
        description="Upload all case materials to generate a complete investigative dossier."
      />
      <form onSubmit={handleSubmit}>
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          <div className="space-y-8">
            {/* Crime Scene Pictures */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Camera /> Crime Scene Pictures
                </CardTitle>
                <CardDescription>
                  Upload images from the crime scene.
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid gap-4">
                  <Input
                    id="picture-upload"
                    type="file"
                    multiple
                    accept="image/*"
                    onChange={handlePictureUpload}
                    className="cursor-pointer"
                  />
                  {crimeScenePictures.length > 0 && (
                    <div className="grid grid-cols-3 gap-2">
                      {crimeScenePictures.map((src, index) => (
                        <div key={index} className="relative aspect-square">
                          <Image
                            src={src}
                            alt={`Crime Scene Photo ${index + 1}`}
                            fill
                            className="rounded-md object-cover"
                          />
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>

            {/* Evidence Input */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <FileInput /> Evidence Log
                </CardTitle>
                <CardDescription>
                  List all pieces of evidence and their descriptions.
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Textarea
                  placeholder=""
                  className="min-h-[150px]"
                />
              </CardContent>
            </Card>

            {/* Witness Statements */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Users /> Witness Statements
                </CardTitle>
                <CardDescription>
                  Enter each witness statement clearly.
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Textarea
                  placeholder=""
                  className="min-h-[150px]"
                />
              </CardContent>
            </Card>
          </div>

          <div className="space-y-8">
            {/* Relationships */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Heart /> Relationships with Victim
                </CardTitle>
                <CardDescription>
                  Describe suspects' relationships, motives, and alibis.
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Textarea
                  placeholder=""
                  className="min-h-[150px]"
                />
              </CardContent>
            </Card>

            {/* Forensic Report */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <FlaskConical /> Forensic Report
                </CardTitle>
                <CardDescription>
                  Input findings from the forensic analysis.
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Textarea
                  placeholder=""
                  className="min-h-[150px]"
                />
              </CardContent>
            </Card>

            {/* Submission */}
            <Card className="bg-primary/5 dark:bg-primary/10">
              <CardHeader>
                <CardTitle>Ready for Analysis?</CardTitle>
                <CardDescription>
                  The AI will perform a deep analysis of all provided data to
                  generate a comprehensive investigative dossier.
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Button
                  type="submit"
                  className="w-full"
                  disabled={isLoading}
                  size="lg"
                >
                  {isLoading ? (
                    <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                  ) : (
                    <FileText className="mr-2 h-5 w-5" />
                  )}
                  Generate Dossier
                </Button>
              </CardContent>
            </Card>
          </div>
        </div>
      </form>
    </div>
  );
}
